<script type="text/javascript">
import bus from "@/bus.js"
import {mapState, mapGetters, mapMutations} from 'vuex' //引入state，getters，mutations
import anime from 'animejs/lib/anime.es.js';
import feedBack from '@/components/feedBack.vue'
export default {
	name: 'design',
	props: {

	},
	data(){
		return {
			feedBack_flag: false,
			from_path: '/',
			menu_index: 0,
			menu_lists: [
				{tag:'我的素材', selected: true}, 
				{tag:'已编辑列表', selected: false}, 
				{tag:'收藏列表', selected: false}, 
				{tag:'消费明细', selected: false}
			]
		}
	},
	watch: {
		menu_index: 'menu_tag_func'
	},
	computed: {
		...mapState(['author_id']),
		download_path: function() {
			return this.filter_design_data[this.download_tips_index].design_img
		}

	},
	mounted: function() {
		const me =this;
		this.hoverFunc()
		bus.$on('changeFeedBack', function(data) {
			me.feedBack_flag = data
		})
	},
	methods: {
		prevFunc: function() {
			const me =this;
			// 返回上一页
			// me.$router.go(-1)
			me.$router.push({
				path: me.from_path
			})
			
		},
		menu_tag_func: function() {
			const me =this;
			
			const index = me.menu_index,
					len = me.menu_lists[index].tag.length;
			// 动画
			anime({
				targets: '.menu_line',
				easing: 'spring(1, 60, 10, 0)',
				duration: 400,
				width: function() {
					return .2 * len + 'rem'
				},
				translateX: function() {
					const tar_left = [.69,2.29,3.89,5.49]
					// return 1.8 * index + 'rem'
					// return event.target.offsetLeft/100+ 0.1 + 'rem'
					// const ratio = parseInt(document.documentElement.style.fontSize)/100;
					// const ev_left =event.target.offsetLeft * ratio,
					// 	ev_width = event.target.offsetWidth * ratio,
					// 	line_left =document.getElementsByClassName('menu_line')[0].offsetLeft * ratio,
					// 	line_width = document.getElementsByClassName('menu_line')[0].offsetWidth
						
						// console.log(ev_left)
					// 	console.log(ev_width)
					// 	console.log(line_width)
						// console.log(event)
					// return (ev_left + (ev_width - 80)/2 - line_left)/100  + 'rem'
					if(index == 1) {
						return tar_left[index] + (1 - 1)/2 - .8  + 'rem'
					}else {
						return tar_left[index] + (1 - .8)/2 - .8  + 'rem'
					}
				},
			});

			// 切换路由
			// this.setRouter(index)
		},
		setRouter: function(index) {
			const me =this;
			// 样式判断
			me.menu_lists.forEach((item) => {
				item.selected = false
			})
			me.menu_lists[index].selected = true
			me.menu_index = index;
			// 切换路由
			const routerArrs =['myMaterial', 'edited', 'collect', 'consume']
			this.$router.push(routerArrs[index]).catch(err => {err})
		},
		hoverFunc: function() {
			let cards = document.querySelectorAll(".cards");
			let range = 15;
			let calcValue = function calcValue(a, b) {return (a / b * range - range / 2).toFixed(1);}; 
			let timeout = void 0;
			cards.forEach(function (card) {
				card.addEventListener('mousemove', function (_ref) {
					let x = _ref.x,
					y = _ref.y;
					if (timeout) {
						window.cancelAnimationFrame(timeout);
					}

					timeout = window.requestAnimationFrame(function () {
						let yValue = calcValue(y, window.innerHeight);
						let xValue = calcValue(x, window.innerWidth);
						// console.log(xValue, yValue);


						card.style.transform = "translateX(" + -xValue + "px) translateY(" + yValue + "px)";
					});
				}, false);

			})
		},
	},
	updated: function() {
		this.hoverFunc()
	},
	beforeRouteEnter(to, from, next) {
		next((vm) => {
			vm.from_path = from.path
			vm.hoverFunc()
			vm.setRouter(0)
		})
	},
}

</script>